<template>
  <div class="pdf-container">
    <div class="pdf-operate-btn">
      <!-- 下载 start -->
      <el-button
        type="primary"
        @click="onDownloadPDF"
        icon="el-icon-download"
        :loading="downloadLoading"
        >下载</el-button
      >
      <!-- 下载 end -->

      <!-- 核实 start -->
      <!-- <el-tag v-if="verifyState == 1">已核实</el-tag>
      <el-button
        type="primary"
        @click="onVerify"
        icon="el-icon-circle-check"
        v-if="verifyState == 0"
        >核实</el-button
      > -->
      <!-- 核实 end -->
    </div>

    <!-- <iframe :src="pdfUrl" width="700" height="500" frameborder="1"></iframe> -->
    <a :href="pdfUrl" :download="pdfName" id="a_pdf"></a>
    <div ref="tableContent">
      <!-- 顶部表格数据 start -->
      <el-table
        :data="pdfTopTableData"
        style="width: 100%"
        border
        :show-header="false"
        :row-style="topRowStyle"
      >
        <el-table-column
          v-for="(item, key) in pdfTopTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
        </el-table-column>
      </el-table>
      <!-- 顶部表格数据 start -->

      <!-- 账户表格数据 start -->
      <el-table
        :data="pdfAccountTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
        :span-method="accountSpanMethod"
      >
        <el-table-column
          v-for="(item, key) in pdfAccountTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
        </el-table-column>
      </el-table>
      <!-- 账户表格数据 start -->

      <!-- 展示表格数据 start -->
      <el-table
        :data="pdfTableData"
        style="width: 100%; border-top: none;"
        border
        v-loading="loading"
      >
        <el-table-column
          v-for="(item, key) in pdfTableFields"
          :key="key"
          :prop="item"
          :label="key"
          align="center"
        >
        </el-table-column>
      </el-table>
      <!-- 展示表格数据 start -->

      <!-- 前两列合并 start -->
      <el-table
        :data="pdfMergeTableData"
        style="width: 100%; border-top: none;"
        :span-method="arraySpanMethod"
        border
        :show-header="false"
        :row-style="rowStyle"
      >
        <el-table-column
          v-for="(item, key) in pdfTableFields"
          :key="key"
          :prop="item"
          :label="key"
          align="center"
        >
        </el-table-column>
      </el-table>
      <!-- 前两列合并 end -->

      <!-- 整行合并 start -->
      <el-table
        :data="pdfRowMergeTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
        :row-style="rowStyle"
      >
        <el-table-column
          v-for="(item, key) in pdfRowTableFields"
          :key="key"
          :prop="item"
          :label="key"
          align="center"
        >
        </el-table-column>
      </el-table>
      <!-- 整行合并 end -->

      <!-- 左右两侧 start -->
      <el-table
        :data="pdfTwoFlankTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
      >
        <el-table-column
          v-for="(item, key) in pdfTwoFlankTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
          <template slot-scope="scope">
            <div>
              <template v-if="item == 'dataMatch'">
                <div class="data-text">{{ scope.row.dataMatch }}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
              <template v-else-if="item == 'dataNoMatch'">
                <div class="data-text">{{ scope.row.dataNoMatch }}</div>
                <div class="content-text"></div>
                <div class="company-sign">
                  <div>（公司签章）</div>
                  <div class="manager">经办人：</div>
                  <div>年　　月　　日</div>
                </div>
              </template>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 左右两侧 end -->

      <!-- 整行 start -->
      <el-table
        :data="pdfExplainTableData"
        style="width: 100%; border-top: none;"
        border
        :show-header="false"
      >
        <el-table-column
          v-for="(item, key) in pdfExplainTableFields"
          :key="key"
          :prop="item"
          :label="key"
        >
          <template slot-scope="scope">
            <div class="explain">
              <div style="width: 100%;">{{ scope.row.explain }}</div>
              <div class="explain-footer">
                <div>{{ footerData.companyName }}</div>
                <div>{{ footerData.date }}</div>
              </div>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 整行 end -->
    </div>
  </div>
</template>

<script>
import { formatDate, formatMonth } from '../utils/utils'
export default {
  name: 'PdfStreamAccount',
  props: {
    propData: {
      type: Object,
      required: true
    },
    searchValidateForm: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      downloadLoading: false, // 导出pdf按钮的加载动画
      pdfUrl: '',
      pdfName: '',

      pdfTopTableFields: {
        表格内容: 'content'
      },
      pdfTopTableData: [
        {
          content: '平遥古城景区管理有限公司：'
        },
        {
          content:
            '为了保证会计数据的准确性、及时性，依据后台管理系统订单数据，现将对账期间内与贵公司尚未结算的月度数据列示，如与贵公司核算数据相符，请在本函下方“数据相符”处盖章，如有不符请在“数据不符”处列明不符内容并签章。'
        }
      ], // 顶部表格数据

      pdfAccountTableFields: {
        字段名称: 'fieldName',
        数据名称: 'dataName',
        字段1: 'field1',
        字段2: 'field2',
        字段3: 'field3',
        字段4: 'field4'
      },
      pdfAccountTableData: [
        {
          fieldName: '收款账户',
          dataName: '平遥古城景区管理有限公司'
        },
        {
          fieldName: '收款账号',
          dataName: '0508 0144 0920 0102 334'
        },
        {
          fieldName: '开 户 行',
          dataName: '中国工商银行股份有限公司平遥支行'
        }
      ], // 账户表格数据

      pdfTableFields: {
        //导出Excel表格的表头设置
        系统中景区名称: '景区名称',
        月份: '月份',
        流水: '流水',
        扣平台手续费: '手续费',
        扣增值税差额: '税',
        可分成金额: '可分成金额'
      },
      pdfTableData: [], // pdf对账单汇总表格数据

      pdfMergeTableData: [
        {
          景区名称: '可分成金额小计：',
          月份: '',
          流水: '',
          手续费: '',
          税: '',
          可分成金额: '0'
        },
        {
          景区名称: '分成比例：',
          月份: '',
          流水: '',
          手续费: '',
          税: '',
          可分成金额: '0%'
        },
        {
          景区名称: '分成金额：',
          月份: '',
          流水: '',
          手续费: '',
          税: '',
          可分成金额: '0'
        }
      ], // pdf合并表格数据

      pdfRowTableFields: {
        公式: 'equation'
      },
      pdfRowMergeTableData: [
        {
          equation: ''
        }
      ], // pdf合并表格数据

      pdfTwoFlankTableFields: {
        数据相符: 'dataMatch',
        数据不符: 'dataNoMatch'
      },
      pdfTwoFlankTableData: [
        {
          dataMatch: '1、数据相符，核对无误。',
          dataNoMatch: '2、数据不符，以下为不符内容：'
        }
      ], // pdf两列表格数据（数据核对部分）

      pdfExplainTableFields: {
        说明: 'explain'
      },
      pdfExplainTableData: [
        {
          explain:
            '请贵公司尽快核对，并将已签章对账单反馈给我公司负责对账人员。我公司已代扣代缴6%税率的增值税款，贵公司需提供等税率的增值税专用发票，我公司将在收到核对相符的对账单及增值税专用发票后，安排支付增值税发票上的价税合计金额。'
        }
      ], // pdf底部说明部分表格数据

      footerData: {
        companyName: '平遥九星科技有限公司',
        date: '2021年9月3日'
      },

      verifyState: '0' // 0没核实 1已核实
    }
  },
  created() {
    this.loading = true
    // this.getPdfTableData()
    // this.getVerifyState()
    this.geCompanyId()
    // this.pdfTopTableData[0].content = this.propData.合作主图
  },
  mounted() {},
  methods: {
    // 获取核实状态
    async getVerifyState() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }

      var paymentType = this.searchValidateForm.paymentType
      if (paymentType == '' || !paymentType) {
        paymentType = null
      } else {
        paymentType.map((type, index) => {
          if (type == '1') {
            paymentType[index] = 'WECHAT'
          }
          if (type == '2') {
            paymentType[index] = 'SAVING'
          }
          if (type == '3') {
            paymentType[index] = 'SAVING_WECHAT'
          }
          if (type == '4') {
            paymentType[index] = 'DEPOSIT'
          }
        })

        paymentType = paymentType.join(',')
        // console.log(paymentType);
      }

      const { data: res } = await this.$http.get(
        '/system/account_statement/isVerify',
        {
          params: {
            company: companyId,
            spot: spotId,
            beginDate: beginDate,
            endDate: endDate,
            paymentType: paymentType
          }
        }
      )
      console.log(res)
      if (res.state == '200') {
        this.verifyState = res.data
      }
    },

    // 获取公司信息
    async geCompanyId() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/getSpotIdCompanyIdData',
        {
          params: {
            companyId,
            spotId
          }
        }
      )
      console.log(res)
      if (res.state == '200') {
        var data = res.data
        this.pdfTopTableData[0].content = data.COMPANY_NAME + '：'

        this.pdfAccountTableData[0].dataName = data.COLLECTION_ACCOUNT
        this.pdfAccountTableData[1].dataName = data.COLLECTION_ACCOUNT_NUMBER
        this.pdfAccountTableData[2].dataName = data.BANK

        this.footerData.companyName = data.SUBJECT_NAME

        this.footerData.date = formatDate()
      }
    },

    async verify() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }

      var paymentType = this.searchValidateForm.paymentType
      if (paymentType == '' || !paymentType) {
        paymentType = null
      } else {
        paymentType.map((type, index) => {
          if (type == '1') {
            paymentType[index] = 'WECHAT'
          }
          if (type == '2') {
            paymentType[index] = 'SAVING'
          }
          if (type == '3') {
            paymentType[index] = 'SAVING_WECHAT'
          }
          if (type == '4') {
            paymentType[index] = 'DEPOSIT'
          }
        })

        paymentType = paymentType.join(',')
        // console.log(paymentType);
      }

      const { data: res } = await this.$http.get(
        '/system/account_statement/verify',
        {
          params: {
            company: companyId,
            spot: spotId,
            beginDate: beginDate,
            endDate: endDate,
            paymentType: paymentType
          }
        }
      )
      if (res.state == '200') {
        this.$message({
          type: 'success',
          message: '核实成功'
        })
        this.verifyState = 1
      }
    },

    // 下载pdf
    async onDownloadPDF() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var subjectId = this.searchValidateForm.subjectId
      if (subjectId == '' || !subjectId) {
        subjectId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }

      try {
        this.downloadLoading = true

        const { data: res } = await this.$http.get(
          '/system/statementSummary/download',
          {
            params: {
              companyId, // 我方公司
              subjectId, // 合作单位
              spotId, // 签约景区
              startTime: beginDate,
              endTime: endDate
            },
            responseType: 'arraybuffer'
          }
        )

        const url = window.URL.createObjectURL(
          new Blob([res], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '对账单'
        a.click()
        this.downloadLoading = false

        // this.downloadLoading = true
        // this.pdfUrl = `/system/account_statement/download?company=${companyId}&spot=${spotId}&beginDate=${beginDate}&endDate=${endDate}&paymentType=${paymentType}`
        // this.pdfName = "对账单汇总"
        // // 注意这里需要等待dom加载完成后才能进行下载
        // this.$nextTick(() => {
        //   document.getElementById("a_pdf").click()
        //   this.downloadLoading = false
        // })

        /* 前端导出pdf方式
        this.$confirm('确认导出PDF文件?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.downloadLoading = true
          var promiseObj = this.$PDFOutput(this.$refs["tableContent"])
          promiseObj.then((value) => {
            this.pdfUrl = value
            this.downloadLoading = false

            this.pdfName = "对账单汇总"
            document.getElementById("a_pdf").click()

            this.$message({
              type: 'success',
              message: '下载成功'
            });
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出PDF文件'
          });
        })
        */
      } catch (error) {
        this.$message({
          type: 'error',
          message: '无数据！'
        })
      }
    },
    // 核实
    onVerify() {
      console.log('点击了核实按钮')
      // 如果选择的时间大于等于当月，则不能核实
      if (
        +new Date(this.searchValidateForm.startEndDate[0]) >=
        +new Date(formatMonth())
      ) {
        this.$message({
          type: 'warning',
          message: '不能核实当月及大于当月的数据',
          offset: 100,
          center: true,
          duration: 2000
        })
        return
      }

      if (new Date().getDate() <= 5) {
        this.$message({
          type: 'warning',
          message: '请在5号后再进行结算操作',
          offset: 100,
          center: true,
          duration: 2000
        })
        return
      }

      this.verify()
    },
    getPdfTableData() {
      // 模拟请求
      this.loading = true
      setTimeout(() => {
        let pdfTableData = [
          {
            scenicName: '平遥古城',
            month: '2020年5月',
            stream: '103356.00',
            tencnetFee: '620.14',
            canFenRunAmount: '102735.86',
            fenRunAmount: '20547.17'
          },
          {
            scenicName: '平遥古城',
            month: '2020年5月',
            stream: '103356.00',
            tencnetFee: '620.14',
            canFenRunAmount: '102735.86',
            fenRunAmount: '20547.17'
          },
          {
            scenicName: '平遥古城',
            month: '2020年5月',
            stream: '103356.00',
            tencnetFee: '620.14',
            canFenRunAmount: '102735.86',
            fenRunAmount: '20547.17'
          }
        ]
        this.pdfTableData = pdfTableData
        this.loading = false
      }, 1000)
    },
    topRowStyle({ row, rowIndex }) {
      if (rowIndex == 0) {
        return {
          'font-weight': '800'
        }
      }
    },
    accountSpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第一列
      if (columnIndex === 1) {
        return [1, 5]
      } else if (
        columnIndex === 2 ||
        columnIndex === 3 ||
        columnIndex === 4 ||
        columnIndex === 5
      ) {
      }
    },
    // pdf对账单汇总表格合并
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      // 合并第0列
      if (columnIndex === 0) {
        return [1, 2]
      } else if (columnIndex === 1) {
        // 将第一列设为0
        return [0, 0]
      }

      // 合并2行2列
      if (rowIndex === 2 && columnIndex === 2) {
        return [1, 3]
      } else if (rowIndex === 2) {
        if (columnIndex === 3 || columnIndex === 4) {
          return [0, 0]
        }
      }
    },

    // pdfMergeTableData表格的样式设置
    rowStyle({ row, rowIndex }) {
      return {
        'font-weight': '800'
      }
    }
  },
  watch: {
    propData(val, oldVal) {
      if (val !== oldVal) {
        // console.log(this.propData);
        // 如果propData为空则直接返回
        if (!this.propData) return

        this.pdfTableData = this.propData.list

        var subtotal = this.propData.subtotal
        var 可分成金额小计 = subtotal.可分成金额小计.toFixed(2)
        var 流水小计 = subtotal.流水小计.toFixed(2)
        var 手续费小计 = subtotal.手续费小计.toFixed(2)
        var 税小计 = subtotal.税小计.toFixed(2)

        var 分成比例 = this.propData.shareProportion
        var 分成金额 = this.propData.shareAmount.toFixed(2)

        this.pdfTableFields = {
          系统中景区名称: '景区名称',
          月份: '月份',
          流水: '流水',
          [`扣平台手续费`]: '手续费',
          [`扣增值税差额`]: '税',
          [`可分成金额`]: '可分成金额'
        }

        this.pdfMergeTableData[0].流水 = 流水小计
        this.pdfMergeTableData[0].手续费 = 手续费小计
        this.pdfMergeTableData[0].税 = 税小计
        this.pdfMergeTableData[0].可分成金额 = 可分成金额小计

        this.pdfMergeTableData[1].景区名称 = `分成比例：`

        this.pdfMergeTableData[1][`可分成金额`] = 分成比例

        this.pdfMergeTableData[2].流水 = ``

        this.pdfMergeTableData[2][`可分成金额`] = 分成金额

        this.loading = false
      }
    },
    searchValidateForm(val, oldVal) {
      if (val !== oldVal) {
        if (!this.searchValidateForm) return
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.pdf-operate-btn {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.data-text {
  line-height: 30px;
  font-weight: 800;
}
.content-text {
  line-height: 30px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.company-sign {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  margin-right: 100px;
  line-height: 30px;
}

.company-sign .manager {
  width: 150px;
}

.explain {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.explain-footer {
  line-height: 30px;
  margin-top: 30px;
  margin-right: 100px;
}
</style>
