<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header" style="height: 100px">
        <!-- 搜索表单 start -->
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 请选择我方公司 start -->
          <el-form-item label="" prop="subjectId">
            <el-select
              v-model="searchValidateForm.subjectId"
              filterable
              placeholder="请选择我方公司"
              clearable
            >
              <el-option
                v-for="(item, index) in searchSubjectCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择我方公司 end -->

          <!-- 请选择合作单位 start -->
          <el-form-item label="" prop="companyId">
            <el-select
              v-model="searchValidateForm.companyId"
              filterable
              placeholder="请选择合作单位"
              @change="onChangeSearchCompany"
              clearable
            >
              <el-option
                v-for="(item, index) in searchCompanyOptions"
                :key="index"
                :label="item.COMPANY_NAME"
                :value="item.COMPANY_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择合作单位 end -->

          <!-- 请选择签约景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="请选择签约景区"
              @change="onChangeSearchSpot"
              clearable
            >
              <el-option
                v-for="(item, index) in searchScenicOptions"
                :key="index"
                :label="item.SPOT_NAME"
                :value="item.SPOT_ID"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="">
            <el-select
              v-model="selectDateState"
              filterable
              placeholder="请选择日期统计方式"
              @change="onDateChange"
            >
              <el-option
                v-for="(item, index) in dateStateOptions"
                :key="index"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker
              v-model="searchValidateForm.startEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['00:00:00', '23:59:59']"
              :value-format="valueFormat"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 实际总收入金额 start -->
        <!-- <el-input v-model="realIncome" placeholder="实际总收入金额" style="width: 100px"></el-input> -->
        <!-- <div class="real-income">
          <span>实际收入金额：</span>
          <el-tag size="small">{{ this.realIncome }}</el-tag>
        </div> -->
        <!-- 实际总收入金额 end -->

        <!-- 搜索 start -->
        <div>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="onSearch('searchValidateForm')"
            style="margin-right: 10px"
            >搜索</el-button
          >
        </div>
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 预览 start -->
        <el-button
          type="primary"
          icon="el-icon-view
"
          @click="onPreview"
          >预览</el-button
        >
        <!-- 预览 end -->

        <!-- 导出 Excel start -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-top-right"
          @click="exportExcel"
          >导出Excel</el-button
        >
        <!-- 导出 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <el-dialog
        title="对账单"
        :visible.sync="pdfDialogVisible"
        :before-close="handleClose"
        center
        fullscreen
        class="pdf-dialog"
        v-if="pdfDialogVisible"
      >
        <!-- 对账单汇总表格 start -->
        <pdf-statement-summary
          :propData="previewData"
          :searchValidateForm="searchValidateForm"
        ></pdf-statement-summary>
        <!-- 对账单汇总表格 end -->

        <!-- 底部取消确定按钮 start -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="pdfDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="pdfDialogVisible = false"
            >确 定</el-button
          >
        </span>
        <!-- 底部取消确定按钮 start -->
      </el-dialog>
    </el-container>
  </div>
</template>

<script>
import PdfStatementSummary from '@/components/PdfStatementSummary.vue'
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth } from '../utils/utils.js'
export default {
  name: 'statementSummary',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      tableLoading: true,
      tableFields: {
        //导出Excel表格的表头设置
        合作单位: 'subjectName',
        我方公司: 'companyName',
        后台管理系统景区名称: 'spotName',
        // 对账单景区名称: 'statementScenicSpotName',
        // 景区名称: 'orderScenicSpotName',
        分成比例: 'proportion',
        期间: 'date',
        订单管理: 'wechatMoney',
        储值订单管理: 'savingMoney',
        储值抵扣订单: 'savingWechatMoney',
        押金扣款管理: 'depositMoney',
        收入合计: 'totalMoney',
        扣手续费: 'serviceCharge',
        扣增值税差额: 'taxMoney',
        分成金额: 'shareAmount',
        特殊事项说明: 'notes',
        开户行: 'bank',
        收款账号: 'collectionAccountNumber'
      },
      tableData: [], // 表格数据

      searchCompanyOptions: [], // 搜索的我方公司选项
      searchSubjectCompanyOptions: [], // 搜索的合作公司选项
      searchScenicOptions: [], // 搜索的签约景区选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        }
      ], // 日期统计方式选项
      payOptions: [
        {
          value: '1',
          label: '微信支付'
        },
        {
          value: '2',
          label: '储值支付'
        },
        {
          value: '3',
          label: '微信+储值抵扣'
        },
        {
          value: '4',
          label: '押金抵扣'
        }
      ], // 支付方式选项
      payStateOptions: [
        {
          value: '10',
          label: '进行中'
        },
        {
          value: '20',
          label: '未付款'
        },
        {
          value: '30',
          label: '已付款'
        },
        {
          value: '40',
          label: '交易关闭'
        },
        {
          value: '50',
          label: '免单'
        },
        {
          value: '60',
          label: '全额退款'
        }
      ], // 支付状态选项 (10 进行中订单，20未付款，30 已付款， 40 交易关闭 50 免单 60 全额退款)

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'monthrange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM', // 日期格式

      searchValidateForm: {
        subjectId: '', // 我方公司id
        companyId: '', // 公司id
        spotId: '', // 景区id
        startEndDate: '', // 起止日期
        paymentType: '', // 支付方式
        orderStatus: '' // 支付状态
      }, // 搜索表单验证对象
      searchVF: {
        subjectId: '', // 我方公司id
        companyId: '', // 公司id
        spotId: '', // 景区id
        startEndDate: '', // 起止日期
        paymentType: '', // 支付方式
        orderStatus: '' // 支付状态
      }, // 搜索需要提交的表单

      validateForm: {}, // 表单验证对象

      pdfDialogVisible: false,
      previewData: {}, // 预览数据

      // 搜索表单规则
      searchValidateFormRules: {
        // 实际总收入金额
        realIncome: [
          {
            validator: checkNumberMethod('实际总收入金额'),
            trigger: ['blur', 'change']
          }
        ]
      },

      total: 0, // 总条数
      realIncome: '' // 实际总收入金额
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getValidateForm()
    this.getSubjectCompanyList()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
  },
  mounted() {},
  methods: {
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 获取我方公司列表
     */
    async getSubjectCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/allSubject',
        {}
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchSubjectCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/account_statement/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var companyId = this.searchVF.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var subjectId = this.searchVF.subjectId
      if (subjectId == '' || !subjectId) {
        subjectId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchVF.startEndDate[0]
        endDate = this.searchVF.startEndDate[1]
      }

      this.tableLoading = true
      const { data: res } = await this.$http.get(
        '/system/statementSummary/getStatementSummaryList',
        {
          params: {
            companyId, // 我方公司
            subjectId, // 合作单位
            spotId, // 签约景区
            startTime: beginDate,
            endTime: endDate,
            // pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res);
      this.tableData = res.data
      this.total = res.totals
      this.realIncome = res.type

      this.tableLoading = false
    },

    /**
     * 获取预览列表数据
     */
    async getPreviewTableData() {
      var companyId = this.searchValidateForm.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var subjectId = this.searchValidateForm.subjectId
      if (subjectId == '' || !subjectId) {
        subjectId = null
      }

      var spotId = this.searchValidateForm.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchValidateForm.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchValidateForm.startEndDate[0]
        endDate = this.searchValidateForm.startEndDate[1]
      }

      const { data: res } = await this.$http.get(
        '/system/statementSummary/preview',
        {
          params: {
            companyId, // 我方公司
            subjectId, // 合作单位
            spotId, // 签约景区
            startTime: beginDate,
            endTime: endDate,
          }
        }
      )
      console.log(res)
      if (!res.data) {
        // this.$message({
        //   message: '预览信息为空',
        //   type: 'info',
        //   offset: 100,
        //   center: true,
        //   duration: 1000
        // })
        return
      }
      this.previewData = res.data
    },

    /**
     * 获取导出Excel表格的全部数据(因数据量太大，默认导出当月的全部数据)
     */
    async getExportTableData() {
      var companyId = this.searchVF.companyId
      if (companyId == '' || !companyId) {
        companyId = null
      }

      var subjectId = this.searchVF.subjectId
      if (subjectId == '' || !subjectId) {
        subjectId = null
      }

      var spotId = this.searchVF.spotId
      if (spotId == '' || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginDate, endDate
      if (startEndDate == '' || !startEndDate) {
        beginDate = formatMonth()
        endDate = formatMonth()
      } else {
        beginDate = this.searchVF.startEndDate[0]
        endDate = this.searchVF.startEndDate[1]
      }

      const { data: res } = await this.$http.get(
        '/system/statementSummary/getStatementSummaryExcel',
        {
          params: {
            companyId, // 我方公司
            subjectId, // 合作单位
            spotId, // 签约景区
            startTime: beginDate,
            endTime: endDate,
          },
          responseType: 'arraybuffer' // 这里必须制定响应的数据类型为arraybuffer，否则导出的文件无法打开
        }
      )

      // this.exportTableData = res.data

      if (res) {
        const url = window.URL.createObjectURL(
          new Blob([res], {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
          })
        )
        const a = document.createElement('a')
        console.log(url)
        a.href = url
        a.download = '对账单汇总'
        a.click()

        this.$message({
          type: 'success',
          message: '导出成功',
          offset: 100,
          center: true,
          duration: 1000
        })
      } else {
        this.$message({
          type: 'error',
          message: '导出失败',
          offset: 100,
          center: true,
          duration: 1000
        })
      }
    },

    /**
     * 查询公司名称获取列表数据
     */
    getTableData() {
      // 搜索优化
      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (
        this.searchValidateForm.subjectId == this.searchVF.subjectId &&
        this.searchValidateForm.companyId == this.searchVF.companyId &&
        this.searchValidateForm.spotId == this.searchVF.spotId &&
        this.selectDateState == this.searchSelectDateState &&
        this.searchValidateForm.startEndDate.toString() ==
          this.searchVF.startEndDate.toString() &&
        this.searchValidateForm.paymentType.toString() ==
          this.searchVF.paymentType.toString() &&
        this.searchValidateForm.orderStatus == this.searchVF.orderStatus
      ) {
        return
      }

      // 保存当前搜索的内容
      this.searchVF.subjectId = this.searchValidateForm.subjectId
      this.searchVF.companyId = this.searchValidateForm.companyId
      this.searchVF.spotId = this.searchValidateForm.spotId
      this.searchSelectDateState = this.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate
      this.searchVF.paymentType = this.searchValidateForm.paymentType
      this.searchVF.orderStatus = this.searchValidateForm.orderStatus

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log('调用接口')
      this.initTableData()
    },

    /** 搜索表单中选项变化方法 start */
    /**
     * 设置支付方式
     * @parasm spotId - 景区id(通过景区选项来获取的支付方式)
     */
    setPaymentType(spotId) {
      if (!spotId) {
        return
      }
      // 当前选择的景区选项对象
      var currentScenicOption = this.searchScenicOptions.filter((option) => {
        return option.SPOT_ID == spotId
      })
      // console.log(currentScenicOption[0]);

      var paymentType = []
      // 定义的type值对应的是payOptions中的value值
      var wechat = '1'
      var saving = '2'
      var saving_wechat = '3'
      var deposit = '4'
      // 判断支付方式
      if (currentScenicOption[0].WECHAT == 1) {
        paymentType.push(wechat)
      }
      if (currentScenicOption[0].SAVING == 1) {
        paymentType.push(saving)
      }
      if (currentScenicOption[0].SAVING_WECHAT == 1) {
        paymentType.push(saving_wechat)
      }
      if (currentScenicOption[0].DEPOSIT == 1) {
        paymentType.push(deposit)
      }

      // console.log(paymentType);
      // paymentType.push("1", "2") // 选择完景区后，加载的支付方式
      this.searchValidateForm.paymentType = paymentType
    },

    // 公司选项变化时触发
    async onChangeSearchCompany(companyId) {
      var res = await this.getScenicList(companyId)
      var searchScenicOptions = res.data

      this.searchScenicOptions = searchScenicOptions

      // 如果返回的景区列表为空，则直接返回
      if (searchScenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/account_statement/soptList 接口返回的景区列表为空"
        // })
        this.searchValidateForm.spotId = ''
        return
      }

      var isHasScenic = false
      searchScenicOptions.forEach((item) => {
        if (item.SPOT_ID == this.searchValidateForm.spotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var spotId = searchScenicOptions[0].SPOT_ID
      if (!!companyId && !isHasScenic) {
        this.searchValidateForm.spotId = spotId
      }

      this.setPaymentType(spotId)
    },

    // 景区选项变化时触发
    async onChangeSearchSpot(spotId) {
      // 如果spotId为空，则将支付方式置为空
      if (!spotId) {
        this.searchValidateForm.paymentType = []
      }
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var searchCompanyOptions = res.data

      this.searchCompanyOptions = searchCompanyOptions

      // 如果返回的公司列表为空，则直接返回
      if (searchCompanyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/account_statement/companyList 接口返回的公司列表为空"
        // })
        this.searchValidateForm.companyId = ''
        return
      }

      var isHasCompany = false
      searchCompanyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.searchValidateForm.companyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.searchValidateForm.companyId = searchCompanyOptions[0].COMPANY_ID
      }

      // console.log(spotId);
      // console.log(this.searchScenicOptions);

      this.setPaymentType(spotId)
    },
    /** 搜索表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
        this.valueFormat = 'yyyy-MM'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'datetimerange'
        this.valueFormat = 'yyyy-MM-dd'
      }
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (
            !!this.searchValidateForm.orderScenicSpotName &&
            this.searchValidateForm.paymentType.length == 0
          ) {
            this.$message({
              type: 'warning',
              message: '请先选择支付方式',
              offset: 100,
              center: true,
              duration: 1000
            })
            return
          }
          this.getTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.subjectId = ''
      this.searchValidateForm.companyId = ''
      this.searchValidateForm.spotId = ''
      this.selectDateState = '1' // 重置日期状态，默认选择按月查询
      this.dateType = 'monthrange' // 重置日历，默认选择按月查询
      this.searchValidateForm.startEndDate = ''
      this.searchValidateForm.paymentType = ''
      this.searchValidateForm.orderStatus = ''

      // 重置当前页数
      this.currentPage = 1
      // 重置每页条数
      this.pageSize = 10

      // 重新获取表格数据
      this.getTableData()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 预览
    onPreview() {
      // 判断录入的起止时间是否是按月查询，若不是提示“查询起止时间非整月”
      if (
        !this.searchValidateForm.subjectId ||
        !this.searchValidateForm.companyId
      ) {
        this.$message({
          type: 'warning',
          message: '请选择我方公司和合作公司',
          offset: 100,
          center: true,
          duration: 1000
        })
      } else {
        // 打开PDF对账单
        this.getPreviewTableData()

        this.pdfDialogVisible = true
      }
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()

          this.downloadLoading = false
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 点击右上角关闭pdf弹窗
    handleClose(done) {
      // console.log(done);
      this.pdfDialogVisible = false
    }
  },
  components: {
    PdfStatementSummary
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (value == 'paymentMethod') {
          if(row[value] == '1') {
            result = '微信支付'
          } else if(row[value] == '2') {
            result = '储值支付'
          } else if(row[value] == '3') {
            result = '微信+储值抵扣'
          } else if(row[value] == '4') {
            result = '押金抵扣'
          }
        } else {
          result = row[value]
        }
        return result
      }
    },
  }
}
</script>

